@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');
$font-stack:    "Open Sans", sans-serif;
$primary-color:  #FFFFFF;
//body set up for display for individual devices
body{
    width: 100%;
    height: 100%;
    margin:0;
    color: $primary-color;
    font-family: $font-stack;
}
//gradient is set from top to bottom and view height set to 100%
.home-page, .privacy-page{
    background:linear-gradient(to top, #1F90C0, #3F3CF3 ) ;
    height: 100vh;
    margin:0;  
}
//removal of presets variables 
p, h1, h2{
    padding:0;
    margin:0;
}
a{
    font-weight: bold;
    text-decoration: none;
    color: $primary-color;
}
//<---------HOME PAGE LAYOUT-------------->
.home-page{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
        .group1{
            .your-city{
                font-size: 64px;
                line-height: 87px;  
                font-style: normal;
                font-weight: 400;
            }
        }
        .group2{
            .location{
                font-style: normal;
                font-weight: 700;
                font-size: 72px;
                line-height: 98px;
                animation: puff 1s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
                     @keyframes puff{
                        0% {
                            transform: scale(2);
                            filter: blur(4px);
                            opacity: 0;
                        }
                         100% {
                            transform: scale(1);
                            filter: blur(0px);
                            opacity: 1;
                        }
                    }
            }
            .rectangle{
                width: auto;
                height: 8px;
                top: 620px;
                background: $primary-color;
                animation: slide-right 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
                    @keyframes slide-right {
                         0% {
                            transform: translateX(1000px);
                            opacity: 0;
                        }
                        100% {
                            transform: translateX(0);
                            opacity: 1;
                        }
                    }
                }

            }
            .privacy-link{
                width: 90px;
                height: 18px;
                font-size: 13px;
                line-height: 18px;
                position: absolute;
                bottom: 3.2%;

            }
}


/* Privacy Page */
.group4{
    width: 910px;
    height: 136px;
    padding-left: 32px;
    padding-top: 32px;
}
.privacy-policy{
    margin:0;
    width: 494px;
    height: 82px;   
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 72px;
    line-height: 98px;
    color: #FFFFFF;
}
.info-privacy{
    width: 910px;
    height: 22px;
    font-family: Open Sans;
    padding-top: 32px;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;

}
#home-link{
    padding-top: 805px;
    
}
.home-link{
    display: flex;
   justify-content: center;
    text-decoration: none;
    color: #FFFFFF;
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 18px;
    
    

}